.output {
    display: flex;
    flex-direction: column;
    height:100%;
}

.output__title {
    margin: 0.75rem;
    padding:0;
    line-height:3rem;
    font-size:1.5rem;
}

.output__description {
    margin: 0 0.75rem 0.75rem;
    line-height:1.5rem;
}

.output__content {
    margin: 0 0.75rem 0.75rem;
    border-radius: 0.375rem;
    border: 1px solid #aaa;

    flex:1;
    font-family: monospace;
}

.output__content--error, .output__content--error:focus {
    border-color:#ff0000;
    outline-color: #ff0000;
    outline-style: solid;
}